@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
.Section_section__1D18S {
  margin: 0 auto;
  padding: 10px;

  width: 350px;
  border: 2px solid grey;
  border-radius: 8px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  box-shadow: 11px 11px 14px 4px rgba(34, 60, 80, 0.34);
}

.FeedbackOptions_button__28MRQ {
  border-radius: 5px;
  margin-right: 10px;

  border: none;
  outline: none;
  transition: all 500ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.FeedbackOptions_button__28MRQ:hover {
  transform: scale(1.2);
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  background-color: lightblue;
  /* border: lightslategray 1px solid; */
}

.FeedbackOptions_button__28MRQ:focus {
  transform: scale(1.2);
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  background-color: lightblue;
}

